.itemContainer {
    width: 100%;
    padding: 0 10px;
}

.itemBody{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.item {
    transition-duration: .3s;
    width: 100%;
    border: 2px #ffffff solid;
    margin: 0 0 5px 0;
    padding: 0 0 10px 0;
    background-color: #ffffff;
}

.item:hover {
    transition-duration: .3s;
    border: 2px #3a8cd7 solid;
    box-shadow: 0 0 0;
}

.title{
    text-align: center;
    font-family: Arial;
    background-color: rgba(128, 161, 193, 0.7);
    color: #ffffff!important;
    margin: 0 0 5px 0 ;
    border-radius: 0 10px 0 10px;
}

.imageContainer {
    text-align: center;
}

.imageExample {
    width: 90px;
    margin-bottom: 5px;
    transition: 0.5s;
}

.itemName {
    margin: 0 auto 2px auto;
    font-size: 20px;
    font-weight: bold;
    color: #5c686e;
}

.itemDescription {
    font-size: 12px;
    color: #8d9ba4;
}

.navLink:hover {
    text-decoration: none;
}

.itemCost {
    color: #3a8cd7;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.buyButton {
    background-color: #9099a0;
    color: #ffffff!important;
    padding: 3px 10px 5px 10px;
    cursor: pointer;
    float: right;
}

.buyButton:hover {
    text-decoration: none;
    color: #ffffff;
    background-color: #3a8cd7;
}

.itemNew {
    background-color: #22c083;
    color: #ffffff;
    left: 82px;
    position: relative;
    width: 86px;
    padding: 0 10px 2px 10px;

}
.wrapperOpen{
    display: block;
}
.wrapperClose{
    display: none;
}
.itemQuantity{
    margin: 0 5px;
}
.itemQuantity h3{
    color: #6c757d;
    font-size: 20px!important;
}

.itemFooter{
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: center;
    color: #6c757d!important;
}

.productQuantity{
    width: 40px;
}
.controlsArea{
    display: flex;
    flex-flow: column;
    margin: 0 20px 0 3px;
}
.quantityControls{
    width: 15px;
    height: 15px;
    background-color: rgba(141, 155, 164, 0.62) !important;
    padding: 0;
    margin-bottom: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.addOrder{
    text-align: center;
    background-color: #a1b9d1;
    padding-bottom: 10px;
    border-radius: 10px 0 10px 0;
    margin-top: 5px;
}


@media (max-width: 436px) {

    .item {
        margin: 10px 5px;

    }

}

@media (max-width: 436px) {

    .item {
        margin: 10px 5px;

    }

}